/**
 * æ ¼å¼åŒ–é‡‘é¢ï¼ˆåˆ†è½¬å…ƒï¼Œä¿ç•™ä¸¤ä½å°æ•°ï¼‰
 */
export const formatMoney = (amount: number): string => {
  return (amount / 100).toFixed(2)
}

/**
 * æ ¼å¼åŒ–é‡‘é¢ï¼ˆå…ƒï¼Œæ·»åŠ åƒåˆ†ä½ï¼‰
 */
export const formatMoneyWithComma = (amount: number): string => {
  const yuan = formatMoney(amount)
  const parts = yuan.split('.')
  parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',')
  return parts.join('.')
}

/**
 * æ ¼å¼åŒ–æ—¥æœŸæ—¶é—´
 */
export const formatDateTime = (date: string | Date, format: string = 'YYYY-MM-DD HH:mm:ss'): string => {
  const d = typeof date === 'string' ? new Date(date) : date
  
  const year = d.getFullYear()
  const month = String(d.getMonth() + 1).padStart(2, '0')
  const day = String(d.getDate()).padStart(2, '0')
  const hour = String(d.getHours()).padStart(2, '0')
  const minute = String(d.getMinutes()).padStart(2, '0')
  const second = String(d.getSeconds()).padStart(2, '0')
  
  return format
    .replace('YYYY', String(year))
    .replace('MM', month)
    .replace('DD', day)
    .replace('HH', hour)
    .replace('mm', minute)
    .replace('ss', second)
}

/**
 * æ ¼å¼åŒ–æ—¥æœŸï¼ˆYYYY-MM-DDï¼‰
 */
export const formatDate = (date: string | Date): string => {
  return formatDateTime(date, 'YYYY-MM-DD')
}

/**
 * æ ¼å¼åŒ–æ—¶é—´ï¼ˆHH:mm:ssï¼‰
 */
export const formatTime = (date: string | Date): string => {
  return formatDateTime(date, 'HH:mm:ss')
}

/**
 * æ ¼å¼åŒ–ç›¸å¯¹æ—¶é—´
 */
export const formatRelativeTime = (date: string | Date): string => {
  const d = typeof date === 'string' ? new Date(date) : date
  const now = new Date()
  const diff = now.getTime() - d.getTime()
  
  const minute = 60 * 1000
  const hour = 60 * minute
  const day = 24 * hour
  const month = 30 * day
  const year = 12 * month
  
  if (diff < minute) return 'åˆšåˆš'
  if (diff < hour) return `${Math.floor(diff / minute)}åˆ†é’Ÿå‰`
  if (diff < day) return `${Math.floor(diff / hour)}å°æ—¶å‰`
  if (diff < day * 2) return 'æ˜¨å¤©'
  if (diff < day * 3) return 'å‰å¤©'
  if (diff < month) return `${Math.floor(diff / day)}å¤©å‰`
  if (diff < year) return `${Math.floor(diff / month)}ä¸ªæœˆå‰`
  return `${Math.floor(diff / year)}å¹´å‰`
}

/**
 * æ ¼å¼åŒ–æ‰‹æœºå·ï¼ˆéšè—ä¸­é—´4ä½ï¼‰
 */
export const formatPhone = (phone: string): string => {
  if (!phone || phone.length !== 11) return phone
  return phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
}

/**
 * æ ¼å¼åŒ–åœ°å€
 */
export const formatAddress = (
  province: string,
  city: string,
  district: string,
  detail: string
): string => {
  return `${province}${city}${district}${detail}`
}

/**
 * æ ¼å¼åŒ–æ•°å­—ï¼ˆæ·»åŠ åƒåˆ†ä½ï¼‰
 */
export const formatNumber = (num: number): string => {
  return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

/**
 * æ ¼å¼åŒ–æ–‡ä»¶å¤§å°
 */
export const formatFileSize = (bytes: number): string => {
  if (bytes === 0) return '0 B'
  const k = 1024
  const sizes = ['B', 'KB', 'MB', 'GB', 'TB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
}

/**
 * æ ¼å¼åŒ–ç™¾åˆ†æ¯”
 */
export const formatPercent = (value: number, total: number, decimals: number = 2): string => {
  if (total === 0) return '0%'
  return ((value / total) * 100).toFixed(decimals) + '%'
}

/**
 * æˆªæ–­æ–‡æœ¬
 */
export const truncate = (text: string, length: number, suffix: string = '...'): string => {
  if (text.length <= length) return text
  return text.substring(0, length) + suffix
}

/**
 * æ ¼å¼åŒ–è®¢å•å·ï¼ˆæ·»åŠ ç©ºæ ¼ï¼‰
 */
export const formatOrderNo = (orderNo: string): string => {
  return orderNo.replace(/(.{4})/g, '$1 ').trim()
}

/**
 * è„±æ•èº«ä»½è¯å·
 */
export const formatIdCard = (idCard: string): string => {
  if (!idCard || idCard.length < 18) return idCard
  return idCard.replace(/(\d{6})\d{8}(\d{4})/, '$1********$2')
}

/**
 * æ ¼å¼åŒ–é“¶è¡Œå¡å·
 */
export const formatBankCard = (cardNo: string): string => {
  if (!cardNo) return cardNo
  // æ¯4ä½æ·»åŠ ä¸€ä¸ªç©ºæ ¼
  return cardNo.replace(/(.{4})/g, '$1 ').trim()
}

/**
 * éšè—é“¶è¡Œå¡å·ä¸­é—´éƒ¨åˆ†
 */
export const hideBankCard = (cardNo: string): string => {
  if (!cardNo || cardNo.length < 16) return cardNo
  return cardNo.replace(/(\d{4})\d+(\d{4})/, '$1 **** **** $2')
}
